.App {
  font-family: sans-serif;
  text-align: center;
}

body {
  background: radial-gradient(rgba(255, 128, 0, 0.7), rgb(7, 56, 214));
  min-height: 100vh;
  width: 100%;
}

:root {
    --effect: hover 4s linear infinite;
  }
  *{
     margin:0;
  }
.loader-con {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    background: rgba(0, 0, 0, o.834);
    z-index: 1;
    background-color: rgb(4, 4, 48);
}

  .spinner {
    text-align: center;
  }
  
  .loader-img {
    display: inline-block;
    text-transform: uppercase;
    text-align: center;
    font-size: 6em;
    font-family: arial;
    font-weight: 600;
    transform: scale(.10);
    color: #121212;
    -webkit-text-stroke: 2px gray;
  }
  
.loader-img:nth-child(1) {
    animation: var(--effect);
  }
  
.loader-img:nth-child(2) {
    animation: var(--effect) .125s;
  }
  
.loader-img:nth-child(3) {
    animation: var(--effect) .350s;
  }
  

  @keyframes hover {
    0% {
      transform: scale(1);
      color: #121212;
      -webkit-text-stroke: 2px gray;
    }
  
    20% {
      transform: scale(1);
      color: pink;
      -webkit-text-stroke: 3px red;
      filter: drop-shadow(0 0 6px black)drop-shadow(0 0 1px black)drop-shadow(0 0 3px red)drop-shadow(0 0 5px red)hue-rotate(10turn);
    }
  
    50% {
      transform: scale(1);
      color: #121212;
      -webkit-text-stroke: 3px gray;
    }
    75% {
      transform: scale(1);
      color: #121212;
      -webkit-text-stroke: 3px gray;
    }
    90% {
      transform: scale(1);
      color: #121212;
      -webkit-text-stroke: 3px gray;
    }
  
  
  }